.container {
  width: 100%;
  margin: 0 auto;
  padding-inline: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-inline: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.movie-list {
  background-color: #e9d8dd;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  list-style: none;
  display: flex;
}

.movie-card {
  width: 280px;
  height: 443px;
  margin-bottom: 20px;
  display: block;
}

.movie-card_img {
  width: 280px;
  height: 402px;
  display: block;
}

.movie-card_desc {
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.movie-card_title {
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 2px;
}

.movie-card_info {
  color: #ff6b08;
  margin: 0;
}

.modal {
  opacity: 0;
  height: 500px;
  background-color: #4545df;
  margin: 20px 20px 38px;
  position: relative;
}

.modal__close {
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__buttons {
  display: block;
}

.button {
  text-transform: uppercase;
  height: 44px;
  cursor: pointer;
  background-color: #0000;
  border-radius: 5px;
}

.button--accent {
  color: #fff;
  background-color: #ff6b08;
  border: none;
}

.button--queue {
  border: 1px solid #000;
}

